* {
  box-sizing: border-box;
}

body {
  background-color: #f1f1f1;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  color: #fff;
  text-align: center;
  background-color: #2f303a;
  padding: 20px;
}

h1 {
  margin: 0;
}

main {
  max-width: 1200px;
  flex-direction: column;
  margin: 0 auto;
  padding: 20px;
  display: flex;
}

.search-form {
  background-color: #fff;
  border-radius: 5px;
  margin-bottom: 20px;
  display: flex;
  overflow: hidden;
  box-shadow: 0 2px 6px rgba(0, 0, 0, .2);
}

.search-form input[type="text"] {
  border: none;
  flex: 1;
  padding: 10px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #2196f3;
  border: none;
  border-radius: 0 5px 5px 0;
  padding: 10px 20px;
  transition: all .3s;
}

.search-form button:hover {
  background-color: #1976d2;
}

.gallery {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  display: grid;
}

.photo-card {
  background-color: #fff;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 2px 6px rgba(0, 0, 0, .2);
}

.photo-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.photo-card .info {
  padding: 10px;
}

.photo-card .info-item {
  margin: 0;
  padding: 5px 0;
}

